import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
	statePageData:{title:'',subtitle:'',navtitle:'',btntext:''},  
    cropImage:'',
    materials: [], // 物料列表，用于发物料单时传递仓库的物料列表
    toUser: {}, // 接收者
    sample: [], // 样版
    assignment: {}, // 外发，用于申请外发时传递到申请页
    userInfo: {}, // 用户信息
    factory:{},
  },
  mutations: {
    setCropImage: (state,cropImage) => {
      state.cropImage = cropImage;
    },
    setStatePageData: (state,statePageData) => {
      state.statePageData = statePageData;
    },
    setMaterials: (state, materials) => {
      state.materials = materials;
    },
    setToUser: (state, toUser) => {
      state.toUser = toUser;
    },
    setSample: (state, sample) => {
      state.sample = sample;
    },
    setUserInfo: (state, userInfo) => {
      state.userInfo = userInfo;
    },
    setFactory: (state, factory) => {
      state.factory = factory;
    },
    setAssignment: (state, assignment) => {
      state.assignment = assignment;
    }
    
  }
})

export default store
