import router from './router'
import store from './vuex/store.js'
import 'lib-flexible/flexible.js'
import FastClick from 'fastclick'
import '@/assets/css/reset.css'

import VueI18n from 'vue-i18n'
Vue.use(VueI18n);


// import Vant from 'vant';
